import React, { useState, useEffect } from 'react';

function Support({ user }) {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');

  useEffect(() => {
    const socket = new WebSocket(`wss://digitalgenesis.support`); // Use secure WebSocket connection

    socket.onopen = () => {
      console.log('WebSocket connection established');
    };

    socket.onmessage = (event) => {
      const message = JSON.parse(event.data);
      const sanitizedEmail = user.email.toLowerCase().replace(/[^a-z0-9]/g, '-');
      if (message.email === sanitizedEmail) {
        setMessages((prevMessages) => [...prevMessages, { text: message.text, sender: 'bot' }]);
      }
    };

    socket.onclose = () => {
      console.log('WebSocket connection closed');
    };

    return () => {
      socket.close();
    };
  }, [user.email]);

  const handleSend = async () => {
    if (input.trim() === '') return;

    const newMessage = { text: input, sender: 'user' };
    setMessages([...messages, newMessage]);

    await sendMessageToDiscord(newMessage);

    setInput('');
  };

  const sendMessageToDiscord = async (message) => {
    const response = await fetch('https://digitalgenesis.support/api/send-message', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ ...message, email: user.email }),
    });

    if (!response.ok) {
      console.error('Failed to send message to Discord');
    }
  };

  return (
    <div className="support-container">
      <div className="messages-container">
        {messages.map((msg, index) => (
          <div key={index} className={`message ${msg.sender}`}>
            {msg.text}
          </div>
        ))}
      </div>
      <div className="input-container">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type your message"
        />
        <button onClick={handleSend}>Send</button>
      </div>
    </div>
  );
}

export default Support;
